import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Cookie } from 'ng2-cookies/ng2-cookies';
import { AppService } from './../../app.service';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
@Component({
  selector: 'app-accept-friend-request',
  templateUrl: './accept-friend-request.component.html',
  styleUrls: ['./accept-friend-request.component.css']
})
export class AcceptFriendRequestComponent implements OnInit {

  constructor(
    public appService: AppService,
    public router: Router,
    private toastr: ToastsManager,
    private _route: ActivatedRoute,
    vcr: ViewContainerRef,
  ) {
    this.toastr.setRootViewContainerRef(vcr);

  }

  ngOnInit() {
  }
  //  let resetPasswordToken = this._route.snapshot.paramMap.get('token');

  public acceptRequest: any = () => {
    let that = this;

    let friendRequestId = this._route.snapshot.paramMap.get('friendRequestId');
    let data = {
      friendRequestId: friendRequestId
    }
    this.appService.acceptRequest(data)
      .subscribe((apiResponse) => {
        if (apiResponse.status === 200) {
          this.toastr.success("your  Accepted Requested ");
          setTimeout(() => {
            that.router.navigate(['/list-view']);

          }, 1000)
        } else {
          this.toastr.error(apiResponse.message);
        }

      }, (err) => {
        this.toastr.error("some error occured");
      });

  }

}



