import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { AppService } from './../../app.service';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

@Component({
  selector: 'app-list-view',
  templateUrl: './list-view.component.html',
  styleUrls: ['./list-view.component.css']
})
export class ListViewComponent implements OnInit {
    shareEmail: any;
    showShareInput: boolean;
    showEditedInputvalue: boolean;
    showEditInputValue: any;
  showEditBoxValue: any;
  showEditBox: any;
  friendEmail: string;
  showFriendListInput: any;
  userName: any;
  listValues: any;
  userId: string;
  listName: any;
  showListInputText: any;

  constructor(
    public appService: AppService,
    public router: Router,
    private toastr: ToastsManager,
    vcr: ViewContainerRef,
  ) {
    this.toastr.setRootViewContainerRef(vcr);

  }

  ngOnInit() {
    this.userId = Cookie.get('receiverId');
    this.userName = Cookie.get('receiverName');

    this.getAllLists();
  }

  public getAllLists: any = () => {

    this.appService.getAllLists(this.userId)
      .subscribe((apiResponse) => {
        debugger;
        if (apiResponse.status === 200) {
          this.listValues = apiResponse.data;
          this.toastr.success("List get Successfully");
        } else {
          this.toastr.error(apiResponse.message);
        }

      }, (err) => {
        this.toastr.error("some error occured");
      });
  }

  public goToLogout: any = () => {
    this.router.navigate(['/']);
  }

  public showListInputFunction: any = (value) => {
    this.showListInputText = value;
    if (value == false) {
      this.listName = "";
    }
  }

   public showShareInputFunction: any = (value) => {
    this.showShareInput = value;
    
  }

  public sentFriendRequest: any = (listData) => {
    this.showShareInputFunction(false)
    if(!this.shareEmail) {
      this.toastr.warning('please enter the email ')
    } else {
      let data = {
        friendEmail:this.shareEmail,
        userId:listData.userId,
        listId:listData.listId
      }
      this.appService.sentFriendRequest(data)
        .subscribe((apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("your Friend Request sent Successfully");
          } else {
            this.toastr.error(apiResponse.message);
          }

        }, (err) => {
          this.toastr.error("some error occured");
        });
    
    }
  }

  public editListInputFunction: any = (value) => {
    this.showEditInputValue = value;
    this.showEditedInputvalue = false;
  }


  public showFriendListInputFunction: any = (value) => {
    this.showFriendListInput = value;
    if (value == false) {
      this.friendEmail = "";
    }
  }


  

  public showEditBoxFunction: any = (index) => {
    this.showEditBoxValue = index;
    this.showEditBox = true;
  }

  public cancelEditBoxFunction: any = () => {
    this.showEditBox = false;
  }



  public createListFunction: any = () => {
    debugger;
    if (!this.listName) {
      this.toastr.warning('please enter the list name')

      //  this.toastr.error("please enter the list name")

    } else {
      //  alert("userId"+this.userId);

      let data = {
        listName: this.listName,
        userId: this.userId
      }
      this.showListInputFunction(false);
      this.appService.createListFunction(data)
        .subscribe((apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("your List Created Successfully");
            this.getAllLists(this.userId);
          } else {
            this.toastr.error(apiResponse.message);
          }

        }, (err) => {
          this.toastr.error("some error occured");
        });
    }

  }


  public editListNameFunction: any = (listData) => {
    debugger;
    if (!this.listName) {
      this.toastr.warning('please enter the list name')

      //  this.toastr.error("please enter the list name")

    } else {
      //  alert("userId"+this.userId);

      let data = {
        listName: this.listName,
        userId: this.userId
      }
      this.showListInputFunction(false);
      this.appService.createListFunction(data)
        .subscribe((apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("your List Created Successfully");
            this.getAllLists(this.userId);
          } else {
            this.toastr.error(apiResponse.message);
          }

        }, (err) => {
          this.toastr.error("some error occured");
        });
    }

  }


  public deleteListNameFunction: any = (listData) => {
    debugger;
    let userId = listData.userId;
    let listId = listData.listId;
    this.cancelEditBoxFunction();

     
      this.showListInputFunction(false);
      this.appService.deleteListFunction(userId,listId)
        .subscribe((apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("your List Deleted Successfully");
            this.getAllLists(this.userId);
          } else {
            this.toastr.error(apiResponse.message);
          }

        }, (err) => {
          this.toastr.error("some error occured");
        });
    

  }


}



