import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListViewComponent } from './list-view/list-view.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastModule } from 'ng2-toastr';
import { HttpClientModule } from '@angular/common/http';
import { AcceptFriendRequestComponent } from './accept-friend-request/accept-friend-request.component';


@NgModule({
  imports: [
    CommonModule,
     FormsModule,
    BrowserAnimationsModule,
    HttpClientModule,
    ToastModule.forRoot(),
    RouterModule.forChild([
      { path: 'list-view', component: ListViewComponent },
      {path:'acceptFriend/:friendRequestId',component:AcceptFriendRequestComponent}

    ])
  ],
  declarations: [ListViewComponent, AcceptFriendRequestComponent]
})
export class TodoListModule { }



