import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Cookie } from 'ng2-cookies/ng2-cookies';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/toPromise';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpErrorResponse, HttpParams } from "@angular/common/http";

@Injectable()
export class AppService {

  private url =  'http://localhost:3002';
  // private url = 'http://todolist-backend.testmanoj.com';
  private allCountryNames = 'https://restcountries.eu/rest/v2/all';
  private countryNameUrl  = 'http://country.io/names.json';
  private countryPhoneUrl = 'http://country.io/phone.json';

  constructor(
    public http: HttpClient
  ) {

    

  } // end constructor  


  public getUserInfoFromLocalstorage = () => {

    return JSON.parse(localStorage.getItem('userInfo'));

  } // end getUserInfoFromLocalstorage


  public setUserInfoInLocalStorage = (data) =>{

    localStorage.setItem('userInfo', JSON.stringify(data))


  }

  public signupFunction(data): Observable<any> {

    const params = new HttpParams()
      .set('firstName', data.firstName)
      .set('lastName', data.lastName)
      .set('mobileNumber', data.mobile)
      .set('email', data.email)
      .set('password', data.password)
      .set('countryCode', data.countryCode);

    return this.http.post(`${this.url}/api/v1/todolist/signup`, params);

  } // end of signupFunction function.

  public signinFunction(data): Observable<any> {

    const params = new HttpParams()
      .set('email', data.email)
      .set('password', data.password);

    return this.http.post(`${this.url}/api/v1/todolist/login`, params);
  } // end of signinFunction function.

  public resetFunction(data): Observable<any> {

    const params = new HttpParams()
      .set('resetPassword', data.resetPassword)
      .set('resetPasswordToken', data.resetPasswordToken);

    return this.http.post(`${this.url}/api/v1/todolist/reset`, params);
  } // end of resetFunction .

   public forgotFunction(data): Observable<any> {
    debugger;
    const params = new HttpParams()
      .set('forgotEmail', data.forgotEmail)

    return this.http.post(`${this.url}/api/v1/todolist/forgot`, params);
  } // end of forgotFunction .


  
  public logout(): Observable<any> {

    const params = new HttpParams()
      .set('authToken', Cookie.get('authtoken'))

    return this.http.post(`${this.url}/api/v1/todolist/logout`, params);

  } // end logout function


  public getCountryNames = () => {
    let response = this.http.get(this.allCountryNames);
		return response;

  }

   public getCountryCodes():Observable<any> {
        return this.http.get(this.countryPhoneUrl);

  }

   public createListFunction(data): Observable<any> {

    const params = new HttpParams()
      .set('listName', data.listName)
      .set('userId',data.userId)

    return this.http.post(`${this.url}/api/v1/todolist/createList`, params);
  } 

   public editListFunction(data): Observable<any> {

    const params = new HttpParams()
      .set('listName', data.listName)

    return this.http.post(`${this.url}/api/v1/todolist/:userId/:listId/editList`, params);
  } 

  public deleteListFunction(userId,listId): Observable<any> {

    const params = new HttpParams()

    return this.http.post(`${this.url}/api/v1/todolist/${userId}/${listId}/deleteList`, params);
  } 


  public sentFriendRequest(data): Observable<any> {

    const params = new HttpParams()
      .set('email', data.friendEmail);
     

    return this.http.post(`${this.url}/api/v1/todolist/${data.userId}/${data.listId}/sentFriendRequest`, params);

  } // end of sentFriendRequest function.

  public acceptRequest(data): Observable<any> {

    const params = new HttpParams()
      .set('friendRequestId', data.friendRequestId)

    return this.http.post(`${this.url}/api/v1/todolist/acceptFriend`, params);
  } // end of resetFunction .

    



     public getAllLists(userId): Observable<any> {

    

    return this.http.get(`${this.url}/api/v1/todolist/getAllLists/${userId}`);
  } 

  

  private handleError(err: HttpErrorResponse) {

    let errorMessage = '';

    if (err.error instanceof Error) {

      errorMessage = `An error occurred: ${err.error.message}`;

    } else {

      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;

    } // end condition *if

    console.error(errorMessage);

    return Observable.throw(errorMessage);

  }  // END handleError

}



